.button {
  border: none;
  background-color: #ff4343;
  padding: 15px 95px;
  margin: 0 auto;
  cursor: pointer;

  &:hover {
    background-color: #e71414;
  }

  &:active {
    box-shadow: 4px 4px 30px 0px #ff4343;
  }
}

.title {
  color: #fff;
  font-family: Raleway;
  font-weight: 900;
  font-size: 24px;
  line-height: 100%;
  letter-spacing: 0%;
  text-align: center;
  vertical-align: middle;
  text-transform: uppercase;
}
